<script>
    import {root} from "../state/trackTree";
    import {fade} from "svelte/transition";
    import {configStore} from "../state/settings";
    import {request} from "../broker"
    import TreeVisBranch from "./TreeVisBranch.svelte";

    $: branchState = $root;
    $: pendingLoad = branchState.pendingLoad;

    $: childStores = branchState.children;
    $: children = Object.entries(childStores);

    function leftClick(mouseEvent) {
        if (mouseEvent.button === 0) {
            if (mouseEvent.ctrlKey) {
                if (branchState !== null) {
                    request($configStore, root, branchState);
                }
            } else {
                root.select([]);
            }
        }
    }

    function rightClick() {
        children.map(pair => pair[0]).forEach(idx => root.deleteChild(idx));
    }
</script>

<style>
.column {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}

.node {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;

    width: 50px;
    height: 50px;
    margin: 10px;
    border-radius: 50%;

    cursor: pointer;
    background: red;
}

.pendingLoad {
    font-size: 18px;
    color: white;
}

.row {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    border-left: 1px solid white;

    border-top: 1px solid white;
    border-right: 1px solid white;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
}

/*# sourceMappingURL=data:application/json;base64, */</style>

<div class="column" transition:fade>
    <div on:mousedown={leftClick} on:contextmenu|preventDefault={rightClick} class="node"></div>
    {#if pendingLoad > 0}
        <span class="pendingLoad" transition:fade>
                +{pendingLoad}
        </span>
    {/if}
    <div class="row">
        {#each children as [index, child] (index)}
            <TreeVisBranch parentStore={root} branchStore={child}/>
        {/each}
    </div>
</div>